        width: 98%;
        aspect-ratio: 1/.7;
    }
    h2{
        margin-bottom: 1rem;
    }
    p{
        margin-top: 1rem;
        color: #333;
    }
    
    button{
        margin-top: 1rem;
        width: 70%;
        font-size: 1.5rem;
        border-radius: 20%;
